import classNames from 'classnames';
import React, { ComponentProps } from 'react';

import { ElementTypes, useUIMode } from 'client-web-sdk';

import { ReactComponent as DragHandleIcon } from './drag-handle.svg';
/* eslint css-modules/no-unused-class: [2, { markAsUsed: ['light', 'dark'] }] */
import styles from './GlobalSearchShortcutInfo.module.css';

interface OwnProps<E extends ElementTypes> {
  element?: E;
  keyLabels?: string[];
  description: string;
  successDescription?: string;
  success?: boolean;
  descriptionButtonStyle?: boolean;
}

type Props<E extends ElementTypes> = OwnProps<E> & Omit<ComponentProps<E>, keyof OwnProps<E>>;

export default function GlobalSearchShortcutInfo<E extends ElementTypes>({
  element,
  keyLabels,
  success,
  descriptionButtonStyle,
  description,
  successDescription,
  ...restProps
}: Props<E>) {
  const Element = element ?? 'div';

  const uiMode = useUIMode();

  return (
    <Element
      className={classNames(
        styles.GlobalSearchShortcutInfo,
        descriptionButtonStyle && styles.descriptionButton,
        styles[uiMode],
        success && styles.success,
      )}
      {...restProps}
    >
      {keyLabels?.map((k, i) => (
        <span key={i} className={styles.Key}>
          {k}
        </span>
      ))}
      <div className={classNames(styles.Description)}>
        {descriptionButtonStyle && restProps.draggable && (
          <DragHandleIcon className={styles.DragHandleIcon} />
        )}
        <span className={classNames(styles.DefaultText)}>{description}</span>
        <span className={classNames(styles.SuccessText)} aria-hidden={!success}>
          {successDescription ?? description}
        </span>
      </div>
    </Element>
  );
}
