import { t } from '@lingui/macro';
import { useLingui } from '@lingui/react';
import React, { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import { Virtuoso, VirtuosoHandle } from 'react-virtuoso';

import { AuditlogEventType, debugConsole, LoginId, trackError, UUID } from 'backend-client-web';
import {
  filterLogins,
  getValueWithPlaceholder,
  LoginUiType,
  ProtectedValue,
  UnprotectedValue,
} from 'client-core';
import {
  getPermissionHint,
  isApplePlatform,
  SearchInput,
  useAuditlogWriteContext,
  useElementSize,
  useModifierKeyName,
  useUIMode,
} from 'client-web-sdk';
import { getWebextShortcutKeyLabels } from 'client-web-sdk/headless';
import {
  calculateTotp,
  DEFAULT_TOTP_ALGORITHM,
  DEFAULT_TOTP_DIGITS,
  DEFAULT_TOTP_PERIOD,
} from 'vault-crypto';

import { ReactComponent as HeyloginIconLight } from 'src/images/heylogin_icon.svg';
import { ReactComponent as HeyloginIconDark } from 'src/images/heylogin_icon_dark.svg';
import { getFrontendServerHost, getFrontendServerUrl } from 'src/serverUrl';
import { ContentLogin, GlobalSearchAction, GlobalSearchActionShortcut } from 'src/types';
import isEmptyValue from 'src/util/isEmptyValue';

import styles from './GlobalSearch.module.css';
import GlobalSearchResultItem from './GlobalSearchResultItem';
import GlobalSearchShortcutButton from './GlobalSearchShortcutButton';
import GlobalSearchShortcutInfo from './GlobalSearchShortcutInfo';

interface Props {
  logins: readonly ContentLogin[] | undefined;
  searchString: string;
  setSearchString: (s: string) => void;
  // highlightIndex === undefined indicated that the search input itself is the active element
  highlightedIndex: number | undefined;
  setHighlightedIndex: (i: number | undefined) => void;
  highlightedItemExpanded: boolean;
  setHighlightedItemExpanded: (e: boolean) => void;
  close: () => void;
  onHeightChange: (height: number) => void;
  openUrl: (url: string) => void;
  checkTotpTimeOffset: () => Promise<void>;
  globalSearchShortcut: string | undefined;
  copy: (value: Promise<string | undefined> | string, clearAfterTimeout?: boolean) => void;
  copied: boolean;
  getUnprotectedValueForContentLogin: (
    l: ContentLogin,
    value: ProtectedValue | UnprotectedValue,
  ) => Promise<string | undefined>;
}

export default function GlobalSearch({
  searchString,
  setSearchString,
  highlightedIndex,
  setHighlightedIndex,
  highlightedItemExpanded,
  setHighlightedItemExpanded,
  logins,
  close,
  onHeightChange,
  openUrl,
  checkTotpTimeOffset,
  globalSearchShortcut,
  copy,
  copied,
  getUnprotectedValueForContentLogin,
}: Props) {
  const [sizeRootRef, contentSize] = useElementSize<HTMLDivElement>();
  useEffect(() => {
    onHeightChange(contentSize.height);
  }, [contentSize.height, onHeightChange]);

  const searchInputRef = useRef<HTMLInputElement | null>(null);
  useFocusInput(searchInputRef);

  const virtuosoHandleRef = useRef<VirtuosoHandle>(null);

  const results = useMemo(() => {
    if (!logins) {
      return undefined;
    }
    if (!searchString) {
      return [];
    }
    return filterLogins(logins, searchString);
  }, [logins, searchString]);

  const resultCount = results ? results.length : undefined;
  const handleArrowKey = useCallback(
    (direction: number) => {
      if (resultCount === undefined) {
        return;
      }
      const oldHighlightedIndex = highlightedIndex;

      // Determine new highlighted index and cap to value range [undefined, 0, ..., results.length - 1]
      let newHighlightedIndex;
      if (oldHighlightedIndex !== undefined) {
        newHighlightedIndex = oldHighlightedIndex + direction;
      } else if (direction === 1) {
        // Skip the first item, as it is already default highlighted
        newHighlightedIndex = 1;
      }
      if (newHighlightedIndex !== undefined && newHighlightedIndex < 0) {
        newHighlightedIndex = undefined;
      } else if (newHighlightedIndex !== undefined && newHighlightedIndex >= resultCount) {
        newHighlightedIndex = resultCount - 1;
      }

      // Apply change
      if (newHighlightedIndex !== oldHighlightedIndex) {
        setHighlightedIndex(newHighlightedIndex);
        setHighlightedItemExpanded(false);
      }

      // Move cursor/selection in text box according to highlight change. This is largely based on the
      // behavior of macOS spotlight.
      const searchInput = searchInputRef.current;
      if (newHighlightedIndex === undefined) {
        if (oldHighlightedIndex === undefined) {
          // Pressed up while already having text box selected, move cursor to start
          if (searchInput) {
            searchInput.selectionStart = 0;
            searchInput.selectionEnd = 0;
          }
        } else {
          // Pressed up from a result item, select everything
          searchInputRef.current?.select();
        }
      } else if (oldHighlightedIndex === undefined && searchInput) {
        // Moving from search input to result. Moves cursor to the end like arrow down normally does
        searchInput.selectionStart = searchInput.value.length;
        searchInput.selectionEnd = searchInput.value.length;
      }

      // scroll item into view
      if (newHighlightedIndex !== undefined) {
        debugConsole.log('[GlobalSearch]', 'scrolling into view', newHighlightedIndex);
        virtuosoHandleRef.current?.scrollIntoView({ index: newHighlightedIndex });
      }
    },
    [highlightedIndex, resultCount, setHighlightedIndex, setHighlightedItemExpanded],
  );

  // If no login is highlighted actions implicitly target the first login on the list
  const loginForActions = results?.[highlightedIndex ?? 0];
  const globalActions = useGlobalActions({ loginForActions, close, openUrl });
  const { loginActions, copiedActionId } = useLoginActions({
    login: loginForActions,
    openUrl,
    close,
    copy,
    copied,
    getUnprotectedValueForContentLogin,
  });
  const modifierKeyName = useModifierKeyName();

  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if (e.key === 'ArrowDown' || e.key === 'ArrowUp') {
        const direction = e.key === 'ArrowDown' ? 1 : -1;
        handleArrowKey(direction);
        e.preventDefault();
        return;
      }
      // Note that we check for login actions first. This ensures that e.g. the enter key are opens
      // a login while one is select and only opens the webapp otherwise.
      const actions = [...loginActions, ...globalActions];
      const action = actions?.find((a) => {
        return (
          !!a.shortcut.modifierKey === e[modifierKeyName] &&
          !!a.shortcut.shiftKey === e.shiftKey &&
          // Note: e.key is always lower case on mac, but can be upper case on linux when pressed
          // together with shift to we normalize to lower case
          (a.shortcut.key?.toLowerCase() === e.key.toLowerCase() ||
            (a.shortcut.enterKey && e.key === 'Enter') ||
            (a.shortcut.escKey && e.key === 'Escape'))
        );
      });
      if (action) {
        e.preventDefault();
        action.perform()?.catch(trackError);
        return;
      }
    };

    document.addEventListener('keydown', handleKeyDown, true);
    return () => {
      document.removeEventListener('keydown', handleKeyDown, true);
    };
  }, [
    close,
    highlightedIndex,
    handleArrowKey,
    searchString,
    loginActions,
    modifierKeyName,
    setSearchString,
    globalActions,
  ]);

  const permissionHint =
    loginForActions &&
    getPermissionHint({
      uiType: getValueWithPlaceholder(loginForActions.uiType),
      permissions: loginForActions.permissions,
      usePronoun: false,
    });

  const actionsToShow = loginActions?.length
    ? loginActions
    : !searchString
    ? globalActions
    : undefined;

  const globalSearchShortcutKeyLabels = useMemo(
    () =>
      globalSearchShortcut !== undefined
        ? getWebextShortcutKeyLabels(globalSearchShortcut, isApplePlatform())
        : undefined,
    [globalSearchShortcut],
  );

  const uiMode = useUIMode();
  const HeyloginIcon = uiMode === 'dark' ? HeyloginIconDark : HeyloginIconLight;

  return (
    <div ref={sizeRootRef} className={styles.GlobalSearch}>
      <section>
        <SearchInput
          value={searchString}
          placeholder={t`Search all logins`}
          ref={searchInputRef}
          onChange={(e) => {
            setHighlightedIndex(undefined);
            setSearchString(e.currentTarget.value);
          }}
          autoFocus
          clear={() => setSearchString('')}
          inlineUi={<HeyloginIcon className={styles.HeyloginIcon} />}
        />
        {results !== undefined && results.length > 0 ? (
          <ResultsList
            results={results}
            highlightedIndex={highlightedIndex}
            virtuosoHandleRef={virtuosoHandleRef}
            setHighlightedIndex={setHighlightedIndex}
            highlightedItemExpanded={highlightedItemExpanded}
            setHighlightedItemExpanded={setHighlightedItemExpanded}
            loginActions={loginActions}
            copiedActionId={copiedActionId}
            checkTotpTimeOffset={checkTotpTimeOffset}
          />
        ) : (
          !!searchString &&
          results !== undefined && <em className={styles.NoResultsHint}>No results found</em>
        )}
      </section>
      {!!actionsToShow?.length && (
        <section className={styles.ShortcutHints}>
          {actionsToShow === globalActions && globalSearchShortcutKeyLabels && (
            <GlobalSearchShortcutInfo
              keyLabels={globalSearchShortcutKeyLabels}
              description={t`Open quick access`}
            />
          )}
          {actionsToShow.map((a) => {
            return (
              <GlobalSearchShortcutButton
                key={a.id}
                shortcut={a.shortcut}
                description={a.description}
                successDescription={a.successDescription}
                success={copiedActionId === a.id}
                onClick={() => {
                  a.perform()?.catch(trackError);
                }}
                dragInfo={a.dragInfo}
              />
            );
          })}
          {permissionHint && <div className={styles.PermissionHint}>{permissionHint}</div>}
        </section>
      )}
    </div>
  );
}

function ResultsList({
  results,
  highlightedIndex,
  virtuosoHandleRef,
  setHighlightedIndex,
  highlightedItemExpanded,
  setHighlightedItemExpanded,
  loginActions,
  copiedActionId,
  checkTotpTimeOffset,
}: {
  results: readonly ContentLogin[];
  highlightedIndex: number | undefined;
  virtuosoHandleRef: React.RefObject<VirtuosoHandle>;
  setHighlightedIndex: (i: number | undefined) => void;
  highlightedItemExpanded: boolean;
  setHighlightedItemExpanded: (e: boolean) => void;
  loginActions: GlobalSearchAction[] | undefined;
  copiedActionId: string | undefined;
  checkTotpTimeOffset: () => Promise<void>;
}) {
  const initialHighlightedItemScrolledIntoViewRef = useRef(false);
  useEffect(() => {
    if (
      !results ||
      highlightedIndex === undefined ||
      initialHighlightedItemScrolledIntoViewRef.current ||
      !virtuosoHandleRef.current
    ) {
      return;
    }
    const tid = window.setTimeout(() => {
      initialHighlightedItemScrolledIntoViewRef.current = true;
      virtuosoHandleRef.current?.scrollIntoView({ index: highlightedIndex });
    }, 10);
    return () => window.clearTimeout(tid);
  }, [highlightedIndex, results, virtuosoHandleRef]);

  // Manually resize container element to list height, so there's no empty space in case of few
  // results. See https://github.com/petyosi/react-virtuoso/issues/652
  const [totalListHeight, setTotalListHeight] = useState(0);

  return (
    <div className={styles.ResultsList} style={{ height: `${totalListHeight}px` }}>
      <Virtuoso
        tabIndex={-1}
        increaseViewportBy={2}
        totalCount={results.length}
        ref={virtuosoHandleRef}
        totalListHeightChanged={(h) => setTotalListHeight(h)}
        // ensure that item at bottom stays visible when expanded
        alignToBottom
        followOutput
        itemContent={(index) => {
          const login = results[index];
          const highlighted = highlightedIndex === index;
          const expanded = highlighted && highlightedItemExpanded;
          return (
            <GlobalSearchResultItem
              key={login.id}
              login={login}
              highlighted={highlighted}
              defaultHighlighted={highlightedIndex === undefined && index === 0}
              onClick={() => {
                setHighlightedIndex(index);
                setHighlightedItemExpanded(
                  index === highlightedIndex ? !highlightedItemExpanded : true,
                );
                virtuosoHandleRef.current?.scrollIntoView({ index, behavior: 'smooth' });
              }}
              last={index === results.length - 1}
              actions={expanded ? loginActions : undefined}
              copiedActionId={copiedActionId}
              checkTotpTimeOffset={checkTotpTimeOffset}
            />
          );
        }}
      />
    </div>
  );
}

function useFocusInput(searchInputRef: React.MutableRefObject<HTMLInputElement | null>) {
  useEffect(() => {
    const handleWindowFocus = () => {
      searchInputRef.current?.focus();
    };
    window.addEventListener('focus', handleWindowFocus);
    return () => {
      window.removeEventListener('focus', handleWindowFocus);
    };
  });
}

function useGlobalActions({
  loginForActions,
  close,
  openUrl,
}: {
  loginForActions: ContentLogin | undefined;
  close: () => void;
  openUrl: (url: string) => void;
}) {
  return useMemo(() => {
    const actions: GlobalSearchAction[] = [];
    if (!loginForActions) {
      const frontendServerHost = getFrontendServerHost();
      actions.push({
        id: 'openWebapp',
        shortcut: { enterKey: true },
        description: t`Open ${frontendServerHost}`,
        perform: () => {
          openUrl(getFrontendServerUrl());
          close();
        },
      });
    }
    actions.push({
      id: 'close',
      shortcut: { escKey: true },
      description: t`Close`,
      perform: close,
    });
    return actions;
  }, [close, loginForActions, openUrl]);
}

function useLoginActions({
  login,
  openUrl,
  close,
  copied,
  copy,
  getUnprotectedValueForContentLogin,
}: {
  login: ContentLogin | undefined;
  openUrl: (url: string) => void;
  close: () => void;
  copy: (value: Promise<string | undefined> | string, clearAfterTimeout?: boolean) => void;
  copied: boolean;
  getUnprotectedValueForContentLogin: (
    l: ContentLogin,
    value: ProtectedValue | UnprotectedValue,
  ) => Promise<string | undefined>;
}) {
  const [copiedActionId, setCopiedActionId] = useState<string>();
  const { i18n } = useLingui();
  const { writeAuditlogEvent } = useAuditlogWriteContext();

  const loginActions = useMemo(() => {
    if (!login) {
      return [];
    }

    function makeCopyAction({
      field,
      loginId,
      name,
      getValue,
      isProtected,
      auditlogEventType,
      auditlogEventCustomFieldId,
      ...rest
    }: {
      field: string;
      loginId: LoginId;
      shortcut: GlobalSearchActionShortcut;
      name: string;
      getValue: () => Promise<string | undefined> | string;
      auditlogEventType?: AuditlogEventType;
      auditlogEventCustomFieldId?: UUID;
      isProtected?: boolean;
    }): GlobalSearchAction {
      function writeAuditlog() {
        if (login?.organization && auditlogEventType) {
          writeAuditlogEvent(login.organization.id, {
            type: auditlogEventType,
            loginId: login.id,
            vaultId: login.vaultMetadata?.id,
            customFieldId: auditlogEventCustomFieldId,
            loginRevisionId: login.history?.currentRevisionId,
            loginEditTime: login.editTime,
          });
        }
      }

      const id = `${loginId}-${field}`;
      const uppercaseName = name.length
        ? name[0].toLocaleUpperCase(i18n.locale) + name.slice(1)
        : name;
      return {
        ...rest,
        id,
        description: t`Copy ${name}`,
        successDescription: t`${uppercaseName} copied!`,
        perform: async () => {
          const valuePromise = getValue();
          copy(valuePromise, isProtected);
          setCopiedActionId(id);
          writeAuditlog();
        },
        dragInfo: {
          getValue,
          onCompleted: () => {
            writeAuditlog();
          },
        },
      };
    }

    const actions: GlobalSearchAction[] = [];

    if (login.websites.length) {
      const website = login.websites[0];
      const attributes = login.websiteAttributes?.[website];
      const url = `${getValueWithPlaceholder(attributes?.protocol) ?? 'http'}://${website}`;
      actions.push({
        id: 'open-website',
        description: t`Open website`,
        shortcut: { enterKey: true },
        perform: () => {
          openUrl(url);
          close();
        },
      });
    }

    if (login.loginUrl) {
      const loginUrl = login.loginUrl;
      actions.push({
        id: 'open-webapp',
        description: t`Open in heylogin`,
        shortcut: { enterKey: true, shiftKey: true },
        perform: () => {
          openUrl(loginUrl);
          close();
        },
      });
    }

    if (login.uiType === LoginUiType.login) {
      if (login.username !== '') {
        actions.push(
          makeCopyAction({
            loginId: login.id,
            field: 'username',
            shortcut: { modifierKey: true, key: 'c' },
            name: t`username`,
            getValue: () => login.username,
          }),
        );
      }
      if (login.permissions.viewSecrets && !isEmptyValue(login.password)) {
        actions.push(
          makeCopyAction({
            loginId: login.id,
            field: 'password',
            shortcut: { modifierKey: true, shiftKey: true, key: 'c' },
            name: t`password`,
            getValue: () => getUnprotectedValueForContentLogin(login, login.password),
            auditlogEventType: AuditlogEventType.LOGIN_R_PASSWORD_COPY,
            isProtected: true,
          }),
        );
      }
      if (login.totp !== undefined) {
        const protectedTotpSecret = login.totp;
        actions.push(
          makeCopyAction({
            loginId: login.id,
            field: 'totp',
            shortcut: { modifierKey: true, key: 't' },
            name: t`TOTP code`,
            getValue: async () => {
              const totpSecret = await getUnprotectedValueForContentLogin(
                login,
                protectedTotpSecret,
              );
              if (!totpSecret) {
                return undefined;
              }
              const { totp } = calculateTotp(
                totpSecret.toUpperCase().trim(),
                getValueWithPlaceholder(login.totpAlgorithm) ?? DEFAULT_TOTP_ALGORITHM,
                login.totpDigits ?? DEFAULT_TOTP_DIGITS,
                login.totpPeriod ?? DEFAULT_TOTP_PERIOD,
              );
              return totp;
            },
          }),
        );
      }
    }
    if (login.uiType === LoginUiType.creditCard && login.creditCard) {
      const creditCard = login.creditCard;
      if (creditCard.number !== '') {
        actions.push(
          makeCopyAction({
            loginId: login.id,
            field: 'number',
            shortcut: { modifierKey: true, key: 'c' },
            name: t`card number`,
            getValue: () => creditCard.number.replaceAll(' ', ''),
          }),
        );
      }
      if (
        creditCard.securityCode !== undefined &&
        login.permissions.viewSecrets &&
        !isEmptyValue(creditCard.securityCode)
      ) {
        const securityCode = creditCard.securityCode;
        actions.push(
          makeCopyAction({
            loginId: login.id,
            field: 'securityCode',
            shortcut: { modifierKey: true, shiftKey: true, key: 'c' },
            name: t`security code`,
            getValue: () => getUnprotectedValueForContentLogin(login, securityCode),
            auditlogEventType: AuditlogEventType.CREDIT_CARD_R_SECURITY_CODE_COPY,
            isProtected: true,
          }),
        );
      }
      if (creditCard.pin !== undefined && login.permissions.viewSecrets) {
        const pin = creditCard.pin;
        actions.push(
          makeCopyAction({
            loginId: login.id,
            field: 'pin',
            shortcut: { modifierKey: true, shiftKey: true, key: 'p' },
            name: t`PIN`,
            getValue: () => getUnprotectedValueForContentLogin(login, pin),
            auditlogEventType: AuditlogEventType.CREDIT_CARD_R_PIN_COPY,
            isProtected: true,
          }),
        );
      }
      if (creditCard.expiration !== '') {
        actions.push(
          makeCopyAction({
            loginId: login.id,
            field: 'expiration',
            shortcut: { modifierKey: true, shiftKey: true, key: 'e' },
            name: t`expiration`,
            getValue: () => creditCard.expiration,
          }),
        );
      }
    }
    if (login.uiType === LoginUiType.note && login.note !== '') {
      actions.push(
        makeCopyAction({
          loginId: login.id,
          field: 'note',
          shortcut: { modifierKey: true, key: 'c' },
          name: t`note`,
          getValue: () => login.note,
        }),
      );
    }
    if (login.uiType === LoginUiType.wifi) {
      if (login.wifiSsid) {
        actions.push(
          makeCopyAction({
            loginId: login.id,
            field: 'wifiSsid',
            shortcut: { modifierKey: true, key: 'c' },
            name: t`wifi network`,
            getValue: () => login.wifiSsid ?? '',
          }),
        );
      }
      if (login.permissions.viewSecrets && !isEmptyValue(login.password)) {
        actions.push(
          makeCopyAction({
            loginId: login.id,
            field: 'password',
            shortcut: { modifierKey: true, shiftKey: true, key: 'c' },
            name: t`password`,
            getValue: () => getUnprotectedValueForContentLogin(login, login.password),
            auditlogEventType: AuditlogEventType.LOGIN_R_PASSWORD_COPY,
            isProtected: true,
          }),
        );
      }
    }

    let customFieldIndex = 0;
    for (const cf of login.customFields) {
      if (customFieldIndex >= 9) {
        break;
      }
      if (cf.name.trim() === '') {
        continue;
      }
      const value = cf.value;
      if (cf.protected && (!login.permissions.viewSecrets || isEmptyValue(cf.value))) {
        continue;
      }
      actions.push(
        makeCopyAction({
          loginId: login.id,
          field: `customField${customFieldIndex}`,
          shortcut: { modifierKey: true, key: `${customFieldIndex + 1}` },
          name: cf.name,
          getValue: () =>
            typeof value === 'string' ? value : getUnprotectedValueForContentLogin(login, value),
          auditlogEventType: AuditlogEventType.LOGIN_R_CUSTOM_FIELD_COPY,
          auditlogEventCustomFieldId: cf.id,
          isProtected: cf.protected,
        }),
      );
      customFieldIndex++;
    }
    return actions;
  }, [
    login,
    i18n.locale,
    writeAuditlogEvent,
    copy,
    openUrl,
    close,
    getUnprotectedValueForContentLogin,
  ]);

  return { loginActions, copiedActionId: copied ? copiedActionId : undefined };
}
