import { Trans } from '@lingui/macro';
import React from 'react';

import { LoginButton, LoginWindow, PushStateType, UnlockDeviceType } from 'client-web-sdk';

import useCloseOnEscape from 'src/util/react/useCloseOnEscape';

interface Props {
  displayEmail?: string;
  pushState: PushStateType | null;
  unlockDeviceType: UnlockDeviceType;
  requestUnlock: () => void;
  cancelLogin: () => void;
  close: () => void;
}

export default function GlobalSearchUnlock({
  displayEmail,
  pushState,
  unlockDeviceType,
  requestUnlock,
  cancelLogin,
  close,
}: Props) {
  useCloseOnEscape(close);

  return (
    <LoginWindow
      state={pushState}
      unlockDeviceType={unlockDeviceType}
      onRetry={requestUnlock}
      onCancel={cancelLogin}
      landscape
    >
      <LoginButton onClick={requestUnlock} size="small">
        {displayEmail || <Trans>Unlock</Trans>}
      </LoginButton>
    </LoginWindow>
  );
}
