import { t } from '@lingui/macro';
import React, { ComponentProps, useEffect, useMemo, useRef } from 'react';

import { debugConsole } from 'backend-client-web';
import { isApplePlatform } from 'client-web-sdk/headless';

import { GlobalSearchActionDragInfo, GlobalSearchActionShortcut } from 'src/types';

import GlobalSearchShortcutInfo from './GlobalSearchShortcutInfo';
interface Props {
  shortcut?: GlobalSearchActionShortcut;
  description: string;
  successDescription?: string;
  success?: boolean;
  onClick: () => void;
  descriptionButtonStyle?: boolean;
  dragInfo?: GlobalSearchActionDragInfo;
}

export default function GlobalSearchShortcutButton({ shortcut, dragInfo, ...rest }: Props) {
  const keyLabels = useMemo(() => {
    if (!shortcut) {
      return undefined;
    }
    const keys: string[] = [];
    // Follow modifier order convention: https://superuser.com/a/1238062
    // Note that we also use the platform-specific convention for how to display keys
    if (isApplePlatform()) {
      if (shortcut.shiftKey) {
        keys.push('⇧');
      }
      if (shortcut.modifierKey) {
        keys.push('⌘');
      }
      if (shortcut.enterKey) {
        keys.push(`↵`);
      }
      if (shortcut.escKey) {
        keys.push(`⎋`);
      }
    } else {
      if (shortcut.modifierKey) {
        keys.push(t`Ctrl`);
      }
      if (shortcut.shiftKey) {
        keys.push(t`Shift`);
      }
      if (shortcut.enterKey) {
        keys.push(t`Enter`);
      }
      if (shortcut.escKey) {
        keys.push(t`Esc`);
      }
    }
    if (shortcut.key) {
      keys.push(shortcut.key.toUpperCase());
    }
    return keys;
  }, [shortcut]);

  const dragHandlers = useDragHandlers(dragInfo);

  return (
    <GlobalSearchShortcutInfo
      element="button"
      type="button"
      keyLabels={keyLabels}
      {...dragHandlers}
      {...rest}
    />
  );
}

const DRAG_COMPLETED_TIMEOUT_MS = 5_000;

function useDragHandlers(dragInfo?: GlobalSearchActionDragInfo): Partial<ComponentProps<'button'>> {
  // The dragEnd event is not emitted consistently. We need that event for our completed handler (to
  // write to auditlog), so we run a timeout and report completed if we don't get a cancel
  // indication within it.
  const dragStatusRef = useRef<{ markDragCanceled: () => void; markDragCompleted: () => void }>();

  // Mark drag as completed if global search close while no completion or cancel signal. This
  // prevents dodging auditlog by closing global search immediately after drag.
  useEffect(() => {
    function handleBeforeUnload() {
      dragStatusRef.current?.markDragCompleted();
    }

    window.addEventListener('beforeunload', handleBeforeUnload);
    return () => window.removeEventListener('beforeunload', handleBeforeUnload);
  });

  // We can't perform an async operation to get the value to drag, so we fetch it as soon as the
  // mouse moves over the button
  const valueRef = useRef<string | undefined>();

  return useMemo(() => {
    if (!dragInfo) {
      return {};
    }
    return {
      draggable: true,
      onMouseEnter: async () => {
        debugConsole.log('[GlobalSearchShortcutButton]', 'mouse enter');
        const value = await dragInfo.getValue();
        valueRef.current = value;
      },
      onMouseLeave: () => {
        debugConsole.log('[GlobalSearchShortcutButton]', 'mouse leave');
        valueRef.current = undefined;
      },
      onDragStart: async (e) => {
        debugConsole.log('[GlobalSearchShortcutButton]', 'drag leave');
        // New drag started before old one finished, assume the previous one succeeded
        if (dragStatusRef.current) {
          dragStatusRef.current.markDragCompleted();
          return;
        }

        const value = valueRef.current;
        valueRef.current = undefined;
        if (!value) {
          e.preventDefault();
          return;
        }
        e.dataTransfer.setData('text/plain', value);

        // Set up drag status and completion timeout
        const cleanup = () => {
          window.clearTimeout(timeoutId);
          dragStatusRef.current = undefined;
        };
        const markDragCompleted = () => {
          cleanup();
          dragInfo.onCompleted();
        };
        const timeoutId = window.setTimeout(markDragCompleted, DRAG_COMPLETED_TIMEOUT_MS);
        dragStatusRef.current = {
          markDragCompleted,
          markDragCanceled: cleanup,
        };
      },
      onDragEnd: (e) => {
        if (e.dataTransfer.dropEffect === 'none') {
          dragStatusRef.current?.markDragCanceled();
        } else {
          dragStatusRef.current?.markDragCompleted();
        }
      },
    };
  }, [dragInfo]);
}
