import { i18n } from '@lingui/core';
import { I18nProvider } from '@lingui/react';
import React from 'react';
import ReactDOM from 'react-dom';

import 'client-web-sdk/index.css';

import { UIModeContextProvider } from 'client-web-sdk';

import initSentry from 'src/initSentry';
import initUiLocale from 'src/locales/initUiLocale';
import { ExtDebugVisibleContextProvider } from 'src/util/react/ExtDebugVisibleContext';

import GlobalSearchApp from './containers/GlobalSearchApp';
import styles from './index.module.css';

document.documentElement.classList.add(styles.globalSearch);

initSentry({ entryPoint: 'globalSearch' });

initUiLocale();

ReactDOM.render(
  <I18nProvider i18n={i18n}>
    <React.StrictMode>
      <ExtDebugVisibleContextProvider>
        <UIModeContextProvider>
          <GlobalSearchApp />
        </UIModeContextProvider>
      </ExtDebugVisibleContextProvider>
    </React.StrictMode>
  </I18nProvider>,
  document.getElementById('root'),
);
