import { Trans } from '@lingui/macro';
import React, { useEffect } from 'react';

import { ActionButton, useElementSize } from 'client-web-sdk';

import { getFrontendServerHost, getFrontendServerUrl } from 'src/serverUrl';
import useCloseOnEscape from 'src/util/react/useCloseOnEscape';

import styles from './GlobalSearchNotPaired.module.css';

interface Props {
  close: () => void;
  onHeightChange: (height: number) => void;
}

export default function GlobalSearchNotPaired({ close, onHeightChange }: Props) {
  const [sizeRootRef, contentSize] = useElementSize<HTMLDivElement>();
  useEffect(() => {
    onHeightChange(contentSize.height);
  }, [contentSize.height, onHeightChange]);

  useCloseOnEscape(close);

  return (
    <div ref={sizeRootRef} className={styles.GlobalSearchNotPaired}>
      <h1>
        <Trans>You are not connected</Trans>
      </h1>
      <p>
        <Trans>
          Open{' '}
          <a
            className={styles.WebappLink}
            href={getFrontendServerUrl()}
            target="_blank"
            rel="noreferrer"
          >
            {getFrontendServerHost()}
          </a>{' '}
          in the browser to connect.
        </Trans>
      </p>
      <p>
        <ActionButton
          buttonStyle="primary"
          element="a"
          href={getFrontendServerUrl()}
          target="_blank"
          rel="noreferrer"
          className={styles.WebappButton}
        >
          <Trans>Open heylogin in the browser</Trans>
        </ActionButton>
      </p>
    </div>
  );
}
