import { Mutex } from 'async-mutex';
import { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import { debounce } from 'throttle-debounce';
import browser from 'webextension-polyfill';

import { Achievement, debugConsole, trackError } from 'backend-client-web';
import { AuditlogWriteContextProvider } from 'client-web-sdk';

import { makeMessageListener, messageError, sendMessage } from 'src/message';
import { ContentLogin } from 'src/types';
import addAchievement from 'src/util/addAchievement';
import { copyToClipboardWithClear } from 'src/util/dom/copyToClipboard';
import getUnprotectedValueForContentLogin from 'src/util/getUnprotectedValueForContentLogin';
import useCopyToClipboard from 'src/util/react/useCopyToClipboard';
import useUnlock from 'src/util/react/useUnlock';

import GlobalSearch from '../components/GlobalSearch';
import GlobalSearchNotPaired from '../components/GlobalSearchNotPaired';
import GlobalSearchUnlock from '../components/GlobalSearchUnlock';

export default function GlobalSearchApp() {
  useEffect(() => {
    addAchievement(Achievement.EXT_GLOBAL_SEARCH_OPEN);
  }, []);

  const initiallyUnlocked = useMemo(() => {
    return new URLSearchParams(window.location.search).has('initiallyUnlocked');
  }, []);

  const { isUnlocked, pushState, requestUnlock, cancelLogin, unlockDeviceType } = useUnlock({
    achievement: Achievement.EXT_GLOBAL_SEARCH_UNLOCK,
    unlockOnInitialRender: true,
  });

  const [searchString, setSearchString] = useState(
    () => new URLSearchParams(window.location.search).get('searchString') ?? '',
  );

  const [highlightedIndex, setHighlightedIndex] = useState<number | undefined>(() => {
    const parsedCandidate = Number(
      new URLSearchParams(window.location.search).get('highlightedIndex'),
    );
    if (Number.isNaN(parsedCandidate)) {
      return undefined;
    }
    return parsedCandidate;
  });

  const [highlightedItemExpanded, setHighlightedItemExpanded] = useState(() => {
    return new URLSearchParams(window.location.search).has('highlightedItemExpanded');
  });

  const saveState = useCallback(() => {
    return sendMessage({
      type: 'SaveGlobalSearchState',
      state: {
        lastUpdateTimestampMs: Date.now(),
        searchString,
        highlightedIndex,
        highlightedItemExpanded,
        top: window.screenTop,
        left: window.screenLeft,
        width: window.outerWidth,
        height: window.outerHeight,
      },
    });
  }, [highlightedIndex, highlightedItemExpanded, searchString]);

  useEffect(() => {
    function handler() {
      saveState().catch(trackError);
    }

    window.addEventListener('beforeunload', handler);
    return () => window.removeEventListener('beforeunload', handler);
  }, [saveState]);

  const { logins, pairing } = useClientCoreData();

  const globalSearchShortcut = useGlobalSearchShortcut();

  const close = useCallback(() => {
    saveState()
      .then(() => window.close())
      .catch(trackError);
  }, [saveState]);

  const openUrl = useCallback((url: string) => {
    sendMessage({ type: 'GlobalSearchOpenUrl', url }).catch(trackError);
  }, []);

  const checkTotpTimeOffset = useCallback(async () => {
    await sendMessage({ type: 'RequestSync' });
  }, []);

  const isLoading = logins === undefined && pairing === undefined;
  const handleHeightChange = useAutoHeight(isLoading);

  // Fallback to unlocked state on open while isUnlocked is not yet populated. This avoids a flash
  // of the unlocked state until we know we're locked.
  const showLockedOverlay = !(isUnlocked ?? initiallyUnlocked);

  const [copied, copy] = useCopyToClipboard(copyToClipboardWithClear);

  return (
    <AuditlogWriteContextProvider
      writeAuditlogEvent={(organizationId, event) => {
        sendMessage({ type: 'WriteAuditlogEvent', organizationId, event }).catch(trackError);
      }}
    >
      {pairing !== undefined && !pairing.paired ? (
        <GlobalSearchNotPaired close={close} onHeightChange={handleHeightChange} />
      ) : showLockedOverlay ? (
        <GlobalSearchUnlock
          displayEmail={pairing?.displayEmail}
          pushState={pushState}
          unlockDeviceType={unlockDeviceType}
          requestUnlock={requestUnlock}
          cancelLogin={cancelLogin}
          close={close}
        />
      ) : (
        <GlobalSearch
          searchString={searchString}
          setSearchString={setSearchString}
          highlightedIndex={highlightedIndex}
          setHighlightedIndex={setHighlightedIndex}
          logins={logins}
          close={close}
          onHeightChange={handleHeightChange}
          openUrl={openUrl}
          highlightedItemExpanded={highlightedItemExpanded}
          setHighlightedItemExpanded={setHighlightedItemExpanded}
          checkTotpTimeOffset={checkTotpTimeOffset}
          globalSearchShortcut={globalSearchShortcut}
          copy={copy}
          copied={copied}
          getUnprotectedValueForContentLogin={getUnprotectedValueForContentLogin}
        />
      )}
    </AuditlogWriteContextProvider>
  );
}

function useAutoHeight(isLoading: boolean) {
  const [contentHeight, setContentHeight] = useState<number>();
  const handleHeightChange = useCallback((height: number) => {
    // height === 0 is an artifact that happens during initial render, ignore to avoid needless
    // resize.
    if (height === 0) {
      return;
    }
    setContentHeight(height);
  }, []);

  const windowHeightMutex = useMemo(() => new Mutex(), []);
  const setWindowInnerHeight = useCallback(
    async (height: number) => {
      await windowHeightMutex.runExclusive(async () => {
        const targetHeight = height + (window.outerHeight - window.innerHeight);
        if (window.outerHeight !== targetHeight) {
          // For chrome this could simply be window.resizeTo, but Firefox doesn't allow that for the
          // window created by the extension. We need to go to the background script to be able to change
          // window size via browser api.
          await sendMessage({ type: 'SetGlobalSearchHeight', height: targetHeight });
        }
      });
    },
    [windowHeightMutex],
  );

  useEffect(() => {
    if (!contentHeight) {
      return;
    }
    // We ignore size changes while stuff is not fully loaded so we don't quickly flicker between
    // sizes during load.
    if (isLoading) {
      return;
    }

    setWindowInnerHeight(contentHeight).catch(trackError);

    // This interval is mostly a no-op since the height is already set above. However sometimes
    // height updates can get lost or lag, so we use it so ensure that the height is set in the end.
    const interval = window.setInterval(() => {
      setWindowInnerHeight(contentHeight).catch(trackError);
    }, 200);
    return () => window.clearInterval(interval);
  }, [contentHeight, isLoading, setWindowInnerHeight]);

  return handleHeightChange;
}

function useClientCoreData() {
  const [clientCoreData, setClientCoreData] = useState<{
    logins?: readonly ContentLogin[];
    pairing?: { paired: true; displayEmail?: string } | { paired: false };
  }>({});

  const updateState = useCallback(() => {
    (async () => {
      const [loginsResponse, pairing] = await Promise.all([
        sendMessage({ type: 'GetLogins', all: true }),
        sendMessage({ type: 'GetUser' }),
      ]);
      setClientCoreData({
        logins: loginsResponse === messageError ? undefined : loginsResponse?.logins,
        pairing:
          pairing === messageError
            ? undefined
            : pairing.userId
            ? { paired: true, displayEmail: pairing.displayEmail }
            : { paired: false },
      });
    })().catch(trackError);
  }, []);

  const stateLoadedInitiallyRef = useRef(false);
  useEffect(() => {
    if (stateLoadedInitiallyRef.current) {
      return;
    }
    stateLoadedInitiallyRef.current = true;
    updateState();
  }, [updateState]);

  useEffect(() => {
    const updateStateDebounced = debounce(100, updateState);

    const messageListener = makeMessageListener({
      DataUpdate: updateStateDebounced,
    });

    browser.runtime.onMessage.addListener(messageListener);
    return () => browser.runtime.onMessage.removeListener(messageListener);
  }, [updateState]);

  return clientCoreData;
}

function useGlobalSearchShortcut() {
  const [globalSearchShortcut, setGlobalSearchShortcut] = useState<string>();

  const updateState = useCallback(() => {
    (async () => {
      const response = await sendMessage({ type: 'GetGlobalSearchShortcut' });
      debugConsole.log('[GlobalSearchApp]', response);
      setGlobalSearchShortcut(
        response === messageError ? undefined : response.globalSearchShortcut,
      );
    })().catch(trackError);
  }, []);

  useEffect(() => {
    updateState();
    const interval = window.setInterval(updateState, 1_000);
    return () => window.clearInterval(interval);
  }, [updateState]);

  return globalSearchShortcut;
}
