import classNames from 'classnames';
import React, { useMemo, useRef } from 'react';

import { trackError, VaultType } from 'backend-client-web';
import {
  getLocalizedLoginDisplayInfo,
  LoginButtonTeamIcon,
  TotpTimeOffsetWarning,
  useElementSize,
  useUIMode,
} from 'client-web-sdk';

import { ContentLogin, GlobalSearchAction } from 'src/types';

/* eslint css-modules/no-unused-class: [2, { markAsUsed: ['light', 'dark'] }] */
import styles from './GlobalSearchResultItem.module.css';
import GlobalSearchShortcutButton from './GlobalSearchShortcutButton';

export default function GlobalSearchResultItem({
  login,
  highlighted,
  defaultHighlighted,
  actions,
  copiedActionId,
  onClick,
  last,
  checkTotpTimeOffset,
}: {
  login: ContentLogin;
  highlighted: boolean;
  defaultHighlighted: boolean;
  actions: GlobalSearchAction[] | undefined;
  copiedActionId: string | undefined;
  onClick: () => void;
  last: boolean;
  checkTotpTimeOffset: () => Promise<void>;
}) {
  const { headline, subheadline, subheadlineMonospace } = useMemo(
    () => getLocalizedLoginDisplayInfo(login),
    [login],
  );
  const uiMode = useUIMode();

  const additionalClickTargetsRef = useRef<HTMLDivElement | null>(null);

  const icon =
    login.vaultMetadata?.type === VaultType.TEAM
      ? 'team'
      : login.vaultMetadata?.type === VaultType.INBOX
      ? 'inbox'
      : undefined;

  return (
    <div
      className={classNames(
        styles.GlobalSearchResultItem,
        styles[uiMode],
        highlighted && styles.highlighted,
        defaultHighlighted && styles.defaultHighlighted,
        last && styles.last,
      )}
      onMouseDown={(e) => {
        if (
          additionalClickTargetsRef.current !== e.target &&
          e.target instanceof Node &&
          additionalClickTargetsRef.current?.contains(e.target)
        ) {
          return;
        }
        onClick();
        e.preventDefault();
      }}
    >
      <header className={classNames(styles.Header, icon && styles.withIcon)}>
        {icon && <LoginButtonTeamIcon icon={icon} className={styles.Icon} />}
        <h3 className={styles.Headline}>{headline}</h3>
        <em className={classNames(styles.Subheadline, subheadlineMonospace && styles.monospace)}>
          {subheadline}
        </em>
      </header>
      <div ref={additionalClickTargetsRef}>
        <TotpTimeOffsetWarning
          className={styles.TotpTimeOffsetWarning}
          hidden={!login.showTotpTimeOffsetWarning || (!highlighted && !defaultHighlighted)}
          recheck={checkTotpTimeOffset}
        />
        <ActionsButtons actions={actions} copiedActionId={copiedActionId} />
      </div>
    </div>
  );
}
function ActionsButtons({
  actions,
  copiedActionId,
}: {
  actions: GlobalSearchAction[] | undefined;
  copiedActionId: string | undefined;
}) {
  const [actionButtonsInnerRef, { height: actionButtonsHeight }] = useElementSize();

  const oldActionsRef = useRef(actions);
  if (actions?.length) {
    oldActionsRef.current = actions;
  }

  return (
    <div
      className={classNames(styles.ActionButtons, actions?.length && styles.expanded)}
      style={{ '--height': `${actionButtonsHeight}px` } as React.CSSProperties}
    >
      <div className={styles.ActionButtonsInner} ref={actionButtonsInnerRef}>
        {(actions ?? oldActionsRef.current)?.map((a) => {
          return (
            <GlobalSearchShortcutButton
              key={a.id}
              description={a.description}
              successDescription={a.successDescription}
              success={copiedActionId === a.id}
              onClick={() => {
                a.perform()?.catch(trackError);
              }}
              dragInfo={a.dragInfo}
              descriptionButtonStyle
            />
          );
        })}
      </div>
    </div>
  );
}
